* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* 主内容区 */
.main-content {
  flex: 1;
  padding: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 180px); /* 减去顶部导航栏和底部横联的高度 */
  transition: all 0.3s ease-in-out;
}

.App {
  min-height: 100vh;
  overflow-x: hidden;
  position: relative;
  width: 100vw;
}

.App::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../public/images/2.jpeg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  animation: gentleBackgroundMove 25s infinite ease-in-out;
  z-index: -1;
}

/* 金字招牌样式 */
.top-banner {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  background: linear-gradient(45deg, #ffd700, #ffed4e, #ffd700);
  padding: 15px 40px;
  border-radius: 50px;
  border: 3px solid #b8860b;
  animation: bannerGlow 3s ease-in-out infinite alternate;
}

.banner-text {
  font-size: 2em;
  font-weight: 900;
  color: #8b0000;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  letter-spacing: 3px;
  font-family: "Microsoft YaHei", sans-serif;
}

@keyframes gentleBackgroundMove {
  0% {
    transform: translate(-0.8%, -0.5%) scale(1.02);
  }
  20% {
    transform: translate(0.5%, -0.8%) scale(1.02);
  }
  40% {
    transform: translate(0.8%, 0.3%) scale(1.02);
  }
  60% {
    transform: translate(-0.3%, 0.8%) scale(1.02);
  }
  80% {
    transform: translate(-0.8%, -0.3%) scale(1.02);
  }
  100% {
    transform: translate(-0.8%, -0.5%) scale(1.02);
  }
}

/* 主布局 */
.main-layout {
  display: flex;
  min-height: 100vh;
  padding-top: 80px; /* 为顶部导航栏留出空间 */
  position: relative;
  z-index: 1;
  background: transparent;
}

/* 顶部导航栏样式 */
.top-nav-bar {
  position: fixed;
  top: 0;
  right: -100%;
  width: calc(100% - 50px);
  height: 80px;
  background: #8c5747;
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  padding: 0 10px 0 20px;
  z-index: 100;
  transition: right 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.top-nav-bar.expanded {
  right: 50px;
}

.nav-header {
  padding: 0 15px;
  text-align: left;
  min-width: 180px;
  flex-shrink: 0;
}

.nav-header h2 {
  color: #ffffff;
  font-size: 1.8em;
  margin-bottom: 5px;
  font-weight: 600;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.nav-menu {
  flex: 1;
  display: flex;
  align-items: stretch;
  justify-content: stretch;
  width: 100%;
  max-width: none;
  margin: 0;
  padding: 0;
  transition: all 0.3s ease;
  opacity: 0;
}

.top-nav-bar.expanded .nav-menu {
  opacity: 1;
  transition-delay: 0.2s;
}

.nav-menu.collapsed {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-20px);
  height: 0;
}

.nav-menu.expanded {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  height: auto;
}

.menu-item {
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
  transform: translateX(20px);
  transition: all 0.3s ease;
  overflow: hidden;
}

.top-nav-bar.expanded .menu-item:nth-child(1) {
  opacity: 1;
  transform: translateX(0);
  transition-delay: 0.3s;
}

.top-nav-bar.expanded .menu-item:nth-child(2) {
  opacity: 1;
  transform: translateX(0);
  transition-delay: 0.4s;
}

.top-nav-bar.expanded .menu-item:nth-child(3) {
  opacity: 1;
  transform: translateX(0);
  transition-delay: 0.5s;
}

.menu-item:not(:last-child)::after {
  content: "";
  position: absolute;
  right: 0;
  width: 2px;
  height: 80px;
  background: #2a1b18;
  pointer-events: none;
}

.menu-button {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  background: transparent;
  border: none;
  color: #ffffff;
  font-size: 1.1em;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  gap: 5px;
  z-index: 2;
}

.menu-button:hover {
  transform: scale(1.05);
}

.menu-icon {
  font-size: 1.2em;
  color: #ffffff;
}

.menu-text {
  font-size: 1.2em;
}

.expand-icon {
  font-size: 0.7em;
  transition: transform 0.3s ease;
}

/* 顶部导航栏展开/收起按钮 */
.nav-toggle {
  position: fixed;
  top: 0;
  right: 0;
  width: 50px;
  height: 80px;
  background: #8c5747;
  backdrop-filter: blur(10px);
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  font-size: 1.5em;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 102;
}

.menu-button:hover {
  transform: translateX(3px);
  box-shadow: none;
}

.menu-button:active {
  transform: translateX(2px);
}

.menu-icon {
  font-size: 1.3em;
  margin-right: 10px;
}

.menu-text {
  flex: 1;
  text-align: left;
}

.menu-link {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.2em;
  padding: 5px 10px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.menu-link:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.05);
}

.expand-icon {
  font-size: 1.2em;
  font-weight: bold;
  color: #ffffff;
  margin-left: 10px;
  transition: transform 0.3s ease;
  position: relative;
  left: -5px;
}

/* 详情面板 */
.detail-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 20px;
  border: 2px solid rgba(255, 215, 0, 0.3);
  max-width: 600px;
  width: 90%;
  animation: fadeSlideIn 0.6s ease-out;
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 1000;
}

.detail-panel.fade-out {
  animation: fadeSlideOut 0.3s ease-in forwards;
}

@keyframes fadeSlideIn {
  from {
    opacity: 0;
    transform: translate(-50%, -45%) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

@keyframes fadeSlideOut {
  from {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
  to {
    opacity: 0;
    transform: translate(-50%, -55%) scale(0.95);
  }
}

.detail-panel h3 {
  font-size: 2em;
  color: #2c3e50;
  margin-bottom: 20px;
  text-align: center;
  border-bottom: 3px solid #ffd700;
  padding-bottom: 15px;
}

.detail-content {
  text-align: center;
  font-size: 1.2em;
  line-height: 1.8;
}

.detail-content p {
  margin-bottom: 15px;
}

.contact-label {
  color: #666;
  font-weight: 600;
  margin-top: 20px;
}

.wechat-contact {
  color: #e74c3c;
  font-size: 1.3em;
  font-weight: bold;
  background: rgba(231, 76, 60, 0.1);
  padding: 10px 20px;
  border-radius: 10px;
  display: inline-block;
}

/* 欢迎面板 */
.welcome-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 60px 40px;
  border-radius: 20px;
  border: 2px solid rgba(255, 215, 0, 0.3);
  max-width: 500px;
  width: 90%;
  z-index: 100;
  animation: welcomeFadeIn 0.6s ease-out;
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

@keyframes welcomeFadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -45%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

.welcome-panel h2 {
  font-size: 2.5em;
  color: #2c3e50;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.welcome-panel p {
  font-size: 1.3em;
  color: #666;
  margin-bottom: 30px;
}

.welcome-decoration {
  display: flex;
  justify-content: center;
  gap: 20px;
  font-size: 2em;
}

.welcome-close-btn {
  background: linear-gradient(45deg, #3498db, #2980b9);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 25px;
  font-size: 1.1em;
  font-weight: 600;
  cursor: pointer;
  margin-top: 20px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
}

.welcome-close-btn:hover {
  transform: translateY(-2px);
}

.contact-label {
  color: #7f8c8d;
  font-size: 0.9em;
  margin: 10px 0 5px 0;
}

.wechat-contact {
  color: #27ae60;
  font-weight: 600;
  font-size: 1.1em;
  background: rgba(39, 174, 96, 0.1);
  padding: 8px 15px;
  border-radius: 20px;
  display: inline-block;
}

/* 响应式设计 */

@media (max-width: 768px) {
  .top-banner {
    padding: 10px 20px;
    top: 10px;
  }

  .banner-text {
    font-size: 1.5em;
    letter-spacing: 2px;
  }

  .main-layout {
    padding-top: 80px;
  }

  .top-nav-bar {
    height: 70px;
  }

  .nav-header {
    min-width: 150px;
    padding: 0 15px;
  }

  .nav-header h2 {
    font-size: 1.5em;
  }

  .nav-header p {
    font-size: 0.8em;
  }

  .nav-menu {
    max-width: 400px;
  }

  .menu-button {
    font-size: 0.9em;
    padding: 10px 15px;
  }

  .menu-icon {
    font-size: 1.1em;
    margin-right: 5px;
  }

  .menu-text {
    margin-right: 5px;
  }

  .nav-toggle {
    width: 50px;
    height: 50px;
    right: 15px;
    font-size: 1.5em;
  }

  .main-content {
    padding: 20px;
  }

  .welcome-panel {
    padding: 40px 20px;
    max-width: 90%;
  }

  .welcome-panel h2 {
    font-size: 2em;
  }

  .detail-panel {
    padding: 20px;
    max-width: 95%;
  }
}